import React, { Component } from 'react';
import Slider from 'react-slick';
import { Toolbar } from '@material-ui/core';
import { Link } from 'react-router-dom';
import { LazyLoadImage } from 'react-lazy-load-image-component';

import image_1 from 'image/category/thoi_trang.png';
import image_2 from 'image/category/sac_dep.png';
import image_3 from 'image/category/thiet_bi_dien_tu.png';
import image_4 from 'image/category/hang_quoc_te.png';
import image_5 from 'image/category/dien_tu_dien_lanh.png';
import image_6 from 'image/category/the_thao_da_ngoai.png';
import image_7 from 'image/category/do_choi_me_va_be.png';
import image_8 from 'image/category/may_anh.png';
import image_9 from 'image/category/dien_gia_dung.png';
import image_10 from 'image/category/bach_hoa.png';
import image_11 from 'image/category/thiet_bi_so.png';
import image_12 from 'image/category/nha_cua_doi_song.png';
import 'scss/carousel.scss';

export default class Responsive extends Component {
  render() {
    const settings = {
      dots: true,
      dotsClass: 'button__bar',
      infinite: false,
      speed: 500,
      slidesToShow: 8,
      slidesToScroll: 4,
      initialSlide: 0,
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 3,
            infinite: true,
            dots: true,
          },
        },
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
            initialSlide: 2,
          },
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
          },
        },
      ],
    };

    return (
      <div>
        <div position="static" className="appbarCategories">
          <Toolbar variant="dense">
            <h4 className="name-categories-wrap"> DANH MC SN PHM </h4>
          </Toolbar>
          <div
            style={{
              backgroundColor: 'rgb(0, 172, 193)',
              width: 200,
              height: 2,
            }}
          >
            .
          </div>
        </div>

        <Slider {...settings}>
          <Link to={{ pathname: '/Thi Trang' }}>
            <div className="image-categories-wrap">
              <LazyLoadImage
                effect="blur"
                src={image_1}
                className="image-categories"
                alt=""
              />
              <div className="title-categories">Thi Trang</div>
            </div>
          </Link>
          <Link to={{ pathname: '/Sc p' }}>
            <div className="image-categories-wrap">
              <LazyLoadImage
                effect="blur"
                src={image_2}
                className="image-categories"
                alt=""
              />
              <div className="title-categories">Sc p</div>
            </div>
          </Link>
          <Link to={{ pathname: '/Thit B in T' }}>
            <div className="image-categories-wrap">
              <LazyLoadImage
                effect="blur"
                src={image_3}
                className="image-categories"
                alt=""
              />
              <div className="title-categories">Thit B in T</div>
            </div>
          </Link>
          <Link to={{ pathname: '/Hng Quc T' }}>
            <div className="image-categories-wrap">
              <LazyLoadImage
                effect="blur"
                src={image_4}
                className="image-categories"
                alt=""
              />
              <div className="title-categories">Hng Quc T</div>
            </div>
          </Link>
          <Link to={{ pathname: '/in T - in Lnh' }}>
            <div className="image-categories-wrap">
              <LazyLoadImage
                effect="blur"
                src={image_5}
                className="image-categories"
                alt=""
              />
              <div className="title-categories">in T - in Lnh</div>
            </div>
          </Link>
          <Link to={{ pathname: '/Th Thao - D Ngoi' }}>
            <div className="image-categories-wrap">
              <LazyLoadImage
                effect="blur"
                src={image_6}
                className="image-categories"
                alt=""
              />
              <div className="title-categories">Th Thao - D Ngoi</div>
            </div>
          </Link>
          <Link to={{ pathname: '/ Chi M V B' }}>
            <div className="image-categories-wrap">
              <LazyLoadImage
                effect="blur"
                src={image_7}
                className="image-categories"
                alt=""
              />
              <div className="title-categories"> Chi M V B</div>
            </div>
          </Link>
          <Link to={{ pathname: '/My nh' }}>
            <div className="image-categories-wrap">
              <LazyLoadImage
                effect="blur"
                src={image_8}
                className="image-categories"
                alt=""
              />
              <div className="title-categories">My nh</div>
            </div>
          </Link>
          <Link to={{ pathname: '/in Gia Dng' }}>
            <div className="image-categories-wrap">
              <LazyLoadImage
                effect="blur"
                src={image_9}
                className="image-categories"
                alt=""
              />
              <div className="title-categories">in Gia Dng</div>
            </div>
          </Link>
          <Link to={{ pathname: '/Bch Ho' }}>
            <div className="image-categories-wrap">
              <LazyLoadImage
                effect="blur"
                src={image_10}
                className="image-categories"
                alt=""
              />
              <div className="title-categories">Bch Ho</div>
            </div>
          </Link>
          <Link to={{ pathname: '/Thit B S' }}>
            <div className="image-categories-wrap">
              <LazyLoadImage
                effect="blur"
                src={image_11}
                className="image-categories"
                alt=""
              />
              <div className="title-categories">Thit B S</div>
            </div>
          </Link>
          <Link to={{ pathname: '/Nh Ca - i Sng' }}>
            <div className="image-categories-wrap">
              <LazyLoadImage
                effect="blur"
                src={image_12}
                className="image-categories"
                alt=""
              />
              <div className="title-categories">Nh Ca - i Sng</div>
            </div>
          </Link>
        </Slider>
      </div>
    );
  }
}
